@font-face {
  font-family: 'fa-custom';
  src: url('../font/fa-custom.eot?42386655');
  src: url('../font/fa-custom.eot?42386655#iefix') format('embedded-opentype'),
       url('../font/fa-custom.woff2?42386655') format('woff2'),
       url('../font/fa-custom.woff?42386655') format('woff'),
       url('../font/fa-custom.ttf?42386655') format('truetype'),
       url('../font/fa-custom.svg?42386655#fa-custom') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
